import axios from 'axios';
import sha1 from 'js-sha1';

export interface ICheckPasswordResult {
    found: boolean;
    count?: number;
}

const API_URL = 'https://api.pwnedpasswords.com';

const hash = (input: string) => sha1(input) as string;
const parsePasswordResponse = (response: string, hashPrefix: string) => response.split('\n').map((line) => {
    const [hashSuffix, count] = line.split(':');
    return {
        hash: hashPrefix + hashSuffix,
        count: count && parseInt(count, 10) || 0,
    };
});

export const checkPassword = (password: string): Promise<ICheckPasswordResult> => {
    const passwordHashed = hash(password).toUpperCase();
    const passwordParameter = passwordHashed.slice(0, 5);

    return axios.get(`${API_URL}/range/${passwordParameter}`)
        .then((response) => {
            if (response.status === 200) {
                const foundPassword = parsePasswordResponse(response.data, passwordParameter)
                                    .find((p) => p.hash === passwordHashed);
                if (foundPassword) {
                    return {
                        found: true,
                        count: foundPassword.count,
                    };
                }
            }
            return {
                found: false,
            };
        })
}