




















import Vue from "vue";
import { ICheckPasswordResult, checkPassword } from "../api/pwned";

interface IHelloWorldData {
  password: string;
  buttonText: string;
  pwned: {
    show: boolean;
    result: ICheckPasswordResult;
  };
  loading: boolean
}

export default Vue.extend({
  name: "HelloWorld",
  props: {
    msg: String
  },
  data() {
    return {
      password: "",
      buttonText: "Have I been PWNED?",
      pwned: {
        result: {
          found: false,
          count: 0
        } as ICheckPasswordResult,
        show: false
      },
      loading: false
    };
  },
  methods: {
    checkPassword(){
      if(this.password.trim()) {
        this.loading = true;
        checkPassword(this.password)
          .then(result => {
            this.pwned.result = result;
            this.pwned.show = true;
          })
          .finally(() => {
            this.loading = false;
          });
      }
    }
  },
  computed: {
    resultBox(this: Vue & IHelloWorldData) {
      return {
        result: true,
        "result--bad": this.pwned.result.found,
        "result--good": !this.pwned.result.found,
      };
    }
  }
});
